.HeroSection {
  margin-left: 72px;
  margin-top: 94px;
  position: relative;
  z-index: 1;
  min-height: 100vh;
}

.HeroBackground {
  position: absolute;
  top: -94px;
  left: 244px;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
  opacity: 50%;
  -webkit-mask-image: linear-gradient(black, transparent);
  mask-image: linear-gradient(black, transparent);
}

.HeroSection h1 {
  font-size: 36px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
}

.HeroSection p {
  font-size: 14px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 30px;
  text-align: left;
  width: 727px;
}

.Heroinput {
  margin-top: 99px;
}

.Heroinput input, button {
  margin-top: 26px;
  margin-left: 49px;
  height: 34px;
  border-radius: 2px;
}

.Heroinput input {
  background-color: #FFFFFF;
  border: #878787;
  box-shadow: inset 0 0 0 1px #878787;
}

.input1 {
  width: 276px;
}

.input2 {
  width: 107px;
  border-right: 4px solid #878787;
}

.input3 {
  width: 107px;
  border-left: none;
}

.input4 {
  width: 52px;
}

.Heroinput button {
  background-color: #0A66BB;
  color: #FFFFFF;
  border: none;
  width: 117px;
  font-size: 14px;
}

.filteredHotels {
  width: 276px;
  border: 1px solid #F5F5F5;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.filtered-child {
  cursor: pointer;
  padding: 10px 20px;
  font-size: 14px;
}

.filtered-child:hover {
  background-color: rgba(10, 102, 187, 0.14);
}

.icons {
  color: #0A66BB;
}