.hotel-list-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    padding: 20px;
  }
  
  .hotel-card {
    width: 393px;
    border: 2px solid #878787;
    border-radius: 4px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .hotel-card-image {
    width: 100%;
    height: 246px;
    object-fit: cover;
  }
  
  .hotel-card-content {
    padding: 15px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .hotel-card-content h3 {
    font-size: 20px;
    font-weight: 400;
    color: #000;
    margin: 0;
  }
  
  .hotel-card-details {
    display: flex;
    gap: 10px;
    align-items: center;
  }
  
  .hotel-card-details .location,
  .hotel-card-details .rating {
    font-size: 14px;
    color: #555;
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .hotel-card-description {
    font-size: 14px;
    color: #555;
    line-height: 1.4;
    margin: 0;
  }
  
  .view-details-button {
    background-color: #0A66BB;
    color: #FFFFFF;
    border: none;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    border-radius: 4px;
    margin-top: 10px;
  }
  
  .view-details-button:hover {
    background-color: #094d8a;
  }