header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    color: rgb(0, 0, 0);
    padding: 10px 20px;
}

header img {
    height: 24px;
    align-self: flex-start;
}

nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

nav ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
}

.left-nav {
    display: flex;
    align-items: center;
}

.left-nav li {
    margin-left: 167px;
}

.left-nav li:first-child {
    margin-left: 0;
}

.left-nav li a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    padding: 5px 10px;
    transition: background-color 0.3s;
    font-size: 16px;
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 400;
}

.right-nav {
    margin-left: auto;
    margin-right: 16px;
}

.right-nav li a {
    color: rgb(0, 0, 0);
    text-decoration: none;
    padding: 5px 10px;
    transition: background-color 0.3s;
    font-size: 16px;
    font-family: 'Inter', Arial, sans-serif;
    font-weight: 400;
}

@media (max-width: 768px) {
    header {
        flex-direction: column;
        align-items: flex-start;
    }

    nav {
        flex-direction: column;
    }

    .left-nav, .right-nav {
        flex-direction: column;
        width: 100%;
    }

    .left-nav li, .right-nav li {
        margin: 10px 0;
    }
}